@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.heading{display:flex;padding-top:3rem;width:100%;justify-content:space-between}.heading .logo{display:flex;flex-direction:column}.heading .logo img{width:120px;height:50px}.heading .date{padding:3rem}.heading .date p{font-size:1.3rem;font-weight:400}

.box-array{margin-top:2rem;width:100%;display:flex;justify-content:space-between}.box-array .box{width:300px;padding:2rem;background-color:white;border-radius:1rem;box-shadow:0px 0px 61px -8px rgba(57,114,227,0.15);transition:transform 0.4s ease-in-out, background-color 0.2s ease-in-out}.box-array .box:hover{transform:translateY(-10px);cursor:pointer;transition:transform 0.4s ease-in-out, background-color 0.2s ease-in-out}.box-array .box hr{margin-top:1rem;margin-right:50%;height:0.3em;border-radius:10px;background-color:var(--lightGreen);border:none}.box-array .box h5{font-size:2.7rem;font-weight:500}.box-array .box p{padding-top:1rem;font-size:1.2rem;font-weight:500}@media (max-width: 1315px){.box-array .box{width:250px}}@media (max-width: 1150px){.box-array{flex-direction:column}.box-array .top,.box-array .bottom-now{display:flex;margin-top:2rem}.box-array .top .box,.box-array .bottom-now .box{margin-left:2rem;flex:1 1}.box-array .top .box hr,.box-array .top .box p,.box-array .bottom-now .box hr,.box-array .bottom-now .box p{display:none}.box-array .top .box h5,.box-array .bottom-now .box h5{font-size:2rem;text-align:center}}@media (max-width: 540px){.box-array .top .box,.box-array .bottom-now .box{margin-left:1rem;margin-right:1rem}.box-array .top .box h5,.box-array .bottom-now .box h5{font-size:1.2rem}}

.graph{flex:2 1;display:flex;align-items:center;height:-webkit-max-content;height:-moz-max-content;height:max-content;margin-top:50px;justify-content:center}

.bottom-container{width:100%;margin-top:1rem;display:flex;flex-wrap:wrap}.bottom-container .news{margin-top:4rem;flex:1 1;flex-basis:350px}.bottom-container .news h5{font-size:2.5rem;color:silver;font-weight:300}.bottom-container .news .news-container{margin-top:1rem;display:flex;flex-direction:column;height:300px}.bottom-container .news .news-container hr{align-self:center;margin-top:2rem;width:30%;border:1px solid #fff;background-color:#e9e9e9;margin-bottom:-1rem}.bottom-container .news .news-container .news-block{margin-top:2.5rem;display:flex;padding-left:2rem;padding-right:10rem}.bottom-container .news .news-container .news-block .right{margin-top:1rem}.bottom-container .news .news-container .news-block .right h6{font-size:1.5rem;color:#757575}.bottom-container .news .news-container .news-block .right p{font-size:1.2rem;margin-top:0.5rem;color:#bebebe;font-weight:500}@media (max-width: 900px){.bottom-container .news .news-container .news-block{padding-right:0rem;padding-left:0rem}}

.verticalcard{flex:1 1;display:flex;flex-direction:column;align-items:center;padding:2rem;flex-basis:300px}.verticalcard .card h4{font-size:2rem;color:#adadad;font-weight:300;text-align:center}.verticalcard .card .lower{margin-top:2rem;display:flex;justify-content:space-between}.verticalcard .card .lower .total,.verticalcard .card .lower .most{display:flex;flex-direction:column;align-items:center}.verticalcard .card .lower .total h5,.verticalcard .card .lower .most h5{font-size:3rem;color:var(--blue);font-weight:300}.verticalcard .card .lower .total p,.verticalcard .card .lower .most p{font-size:1.2rem;font-weight:600;color:#b6b6b6}.verticalcard .card .lower .most h5{color:var(--green)}.verticalcard .radial{width:100%;height:100%;display:flex;justify-content:center;align-items:center}

.man_of_match{flex:1 1;display:flex;flex-direction:column;flex-basis:450px}.man_of_match h4{margin-top:2rem;margin-bottom:2rem;font-size:2rem;color:#adadad;font-weight:300}.man_of_match .block{width:100%;margin-top:2rem}.man_of_match .block .title{display:flex;justify-content:space-between}.man_of_match .block .title p{font-weight:600;font-size:1.3rem;color:#adadad}.man_of_match .block .contain{margin-top:1.4rem;height:10px;border-radius:20px}.man_of_match .block .contain .bar{height:100%;border-radius:20px;transition:all 0.7s ease-out}@media (max-width: 1150px){.man_of_match{margin-top:10rem}}

.pie{width:100%;min-height:500px;margin-top:2rem;display:flex;flex-wrap:wrap}

.loc-toss{width:100%;height:80vh;display:flex;flex-direction:column;justify-content:center;align-items:center}.loc-toss h6{text-align:center;margin-bottom:2rem;margin-top:20rem;color:#adadad;font-weight:300}@media (max-width: 1150px){.loc-toss{margin-top:10rem}}

*{padding:0;margin:0;box-sizing:border-box}html{font-size:62.5%;font-family:"Montserrat", sans-serif;scroll-behavior:smooth;--header1: 5rem;--header2: 4.4rem;--header3: 4rem;--header4: 3.5rem;--header5: 3rem;--header6: 2.5rem;--text-size: 2rem;--backgroundColor: #f5f6fa;--lightBlue: #ecf7fd;--lightGreen: #e7f6f3;--lightOrange: #fff4f0;--lightYellow: "#fffaf4";--lightRed: "#fcf0f0";--green: #19a58a;--orange: #ff8c60;--yellow: #fecc8c;--red: #d07475;--blue: #3972e3}h1{font-size:5rem;font-size:var(--header1);font-weight:500}h2{font-size:4.4rem;font-size:var(--header2);font-weight:500}h3{font-size:4rem;font-size:var(--header3);font-weight:500}h4{font-size:3.5rem;font-size:var(--header4);font-weight:500}h5{font-size:3rem;font-size:var(--header5);font-weight:500}h6{font-size:2.5rem;font-size:var(--header6);font-weight:500}a{text-decoration:none;color:white}ul{list-style:none}li,p,a{font-size:2rem;font-size:var(--text-size);font-weight:300}.App{width:100%;height:200vh;padding-left:5rem;padding-right:5rem}.App .footer{height:500px;display:flex;justify-content:center;align-items:center}.App .footer img{width:120px;height:50px}.App .footer .right{display:flex;flex-direction:column;margin-left:5rem}.App .footer .right p{font-size:2rem}.App .footer .right ul li{margin-top:1rem}.App .footer .right ul li p{font-size:1.5rem}@media (max-width: 540px){.App{padding-left:2rem;padding-right:2rem}}

